.nav{font-size:15px;border-bottom:1px solid #64470b;background-color:#000 !important;padding-left:40px;padding-right:40px}.nav .logo{width:183px;height:26px}.nav .mr-auto{margin-left:45px}.nav .mr-auto .menu{padding-right:10px;color:white;font-size:15px}.nav .mr-auto .menu:hover{border:1px solid;border-radius:2px;color:#eebd22;font-size:15px;height:auto}

.location-title{display:flex;justify-content:space-between;padding-top:2rem}.location-title .title{float:left;font-family:inherit;font-weight:500;line-height:1.1;color:#fff}.location-title .map-view-btn{color:#eebd22;border:1px solid #eebd22;background-color:transparent;border-radius:2px;padding:7px 1.5rem 7px 1.5rem;display:inline-block;text-transform:uppercase}.usa-location-btn{background-color:transparent;border-top-color:transparent;border-left-color:transparent;border-right-color:transparent;border-bottom-color:#959595;outline:0 !important;width:100%;color:#959595}.usa-location-btn .right-arrow-icon{float:right}.usa-location-btn .drop-down-icon{float:right;-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)}.usa-location-btn .drop-down-icon-line{-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)}.usa-location-btn .location-name-btn{float:left;color:#959595}.usa-location-btn:hover{color:#eebd22;border-bottom-color:#eebd22}.usa-location-btn:hover .location-name-btn{color:#eebd22}.states-dropDown{background-color:transparent;color:#959595}.states-dropDown ul{-moz-column-count:6;-moz-column-gap:20px;-webkit-column-count:6;-webkit-column-gap:20px;column-count:6;grid-column-gap:20px;column-gap:20px;list-style-type:none}.drop-down-container{margin-top:30px}.drop-down-container .usa-location{margin-top:50px}.location-others{display:flex;justify-content:space-between;margin-top:50px}.location-others .other-location-btn{background-color:transparent;border-top-color:transparent;border-left-color:transparent;border-right-color:transparent;border-bottom-color:gray;outline:0 !important;width:100%;min-width:380px;color:#959595}.location-others .other-location-btn .right-arrow-icon{float:right}.location-others .other-location-btn .drop-down-icon{float:right;-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)}.location-others .other-location-btn .drop-down-icon-line{-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)}.location-others .other-location-btn .location-name-btn{float:left;color:#959595}.location-others .other-location-btn:hover{color:#eebd22;border-bottom-color:#eebd22}.location-others .other-location-btn:hover .location-name-btn{color:#eebd22}

.footer-items-container{background:black;background-image:url(/Vituel-assistant/static/media/footer-bg.bde7ba91.png)}.footer-items-container .footer-container{display:flex;flex-direction:row;height:auto;width:750px;color:white;padding:40px 0 0 0}.footer-items-container .footer-container .footer-list{display:inline}.footer-items-container .footer-container .footer-list ul{list-style:none;font-size:15px}.footer-items-container .footer-container .footer-list ul li{padding-top:10px;position:relative;padding-right:10px}.footer-items-container .footer-container .footer-list ul li:before{border-right:2px solid gray;border-bottom:2px solid gray;width:9px;height:9px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);content:"";display:inline-block;margin-right:10px}.footer-items-container .footer-container .footer-content-header{color:gray;font-size:20px;padding-bottom:20px}.footer-items-container .footer-container .social-container{margin-left:50px;font-size:15px}.footer-items-container .footer-container .social-container .social-footer-container{margin:10px 0 0 0}.footer-items-container .footer-container .social-container .social-footer-container .social-content{color:gray;margin:70px 0 0 0}.footer-items-container .footer-splashes{width:370px;padding:0  0 20px 40px}.footer-items-container .footer-splashes span{display:flex;justify-content:space-between}

.image-pagination-container{color:#959595;display:flex;justify-content:space-between;width:100%;padding-top:60px}.image-pagination-container .pagination{font-size:15px}.image-pagination-container .pagination .select-pagination{display:flex;height:25px;background-color:#111;border-radius:3px;margin-left:15px}.image-pagination-container .pagination .select-pagination .list-icon{margin-top:5px;margin-left:5px}.image-pagination-container .pagination .select-pagination .list-icon .as-list-Icon{height:4px;width:31px;color:#959595;border-radius:2px;display:block;background-color:#959595;margin-top:2px}.image-pagination-container .pagination .select-pagination .display-text{margin-left:5px;margin-right:5px}.image-pagination-container .pagination .select-pagination .four-list-icon .as-four-list-Icon{display:inline-block;margin-left:5px;height:9px;width:6px;border-radius:2px;background-color:#959595}.image-pagination-container .pagination .select-pagination .six-list-Icon{margin-left:2px}.image-pagination-container .pagination .select-pagination .six-list-Icon .as-six-list-Icon{height:9px;width:4px;border-radius:2px;display:inline-block;background-color:#959595;margin-left:3px}.image-pagination-container .pagination .list-display{background-color:#242424}.image-pagination-container .pagination .list-display span{margin-left:4px;margin-right:4px;border-radius:2px}

.escorts-image-container{display:flex;flex-wrap:wrap;width:100%;height:auto}.escorts-image-container .image-container{margin-right:13px;color:#eebd22;font-size:1em;margin-top:5px}.escorts-image-container .image-container .container{padding-left:0;padding-right:0}.escorts-image-container .image-container .container .row{margin-right:0;margin-left:0}.escorts-image-container .image-container .container .row .col{position:relative;width:100%;padding-right:0;padding-left:0}

