.slider-container{height:400px;margin-bottom:20px}.slider-container .slick-slider{margin-left:60px;margin-right:60px}.slider-container .slick-slider .svg-inline--fa{width:30px;margin-bottom:50px}.slider-container .slick-slider .slick-arrow::before{visibility:hidden}.slider-container .slider-images-container{padding-top:30px}

.profile-container .profile-info-container{margin-top:20px;margin-bottom:20px;height:60px}.profile-container .profile-info-container .profile-header{float:left}.profile-container .profile-info-container .profile-header .escort-info{display:flex;color:#eebd22}.profile-container .profile-info-container .profile-header .escort-info .escort-name{color:white;padding-right:15px}.profile-container .profile-info-container .profile-header .escort-location{color:white}.profile-container .profile-info-container .escort-contact{color:#eebd22;float:right}.profile-container .profile-info-container .escort-contact div{display:flex}.profile-container .image-slider-container{background-color:black}

.about-escort-container{display:flex;color:white;font-size:14px}.about-escort-container .about-me{background-color:#00000091;height:400px;width:800px;border-radius:4px}.about-escort-container .about-me .about-me-container{margin-right:30px;margin-left:20px;margin-top:30px}.about-escort-container .my-details{display:flex;flex-direction:column;background-color:#00000091;margin-left:30px;border-radius:4px}.about-escort-container .my-details .details-container{margin-right:30px;margin-left:20px;margin-top:30px}.about-escort-container .my-details .details-container table{margin-right:20px;margin-top:20px}.about-escort-container .my-details .details-container table tr{height:40px}.about-escort-container .my-details .details-container table tr th{width:150px;color:gray}.about-escort-container .social-media-links{margin-left:30px;padding-top:20px;padding-bottom:10px}.about-escort-container .social-media-links table{height:auto;width:100%;margin-bottom:50px}.about-escort-container .social-media-links table tr{height:50px;border-bottom:0.5px solid gray}.about-escort-container .social-media-links table th{width:25px;font-size:12px}.about-escort-container .contact-details-container{margin-left:30px;margin-top:5px;background-color:#00000091;border-radius:4px}.about-escort-container .contact-details-container .contact-details{margin-right:30px;margin-left:20px;padding-top:20px;padding-bottom:10px}.about-escort-container .contact-details-container .contact-details h5{padding-bottom:10px}.about-escort-container .contact-details-container .contact-details span{color:#eebd22}.about-escort-container .contact-details-container .contact-details .contact{display:block}

.bread-crub{width:100%;background-color:black;height:40px}.bread-crub ul{display:flex;list-style:none;padding-top:10px}.bread-crub ul li{color:#eebd22;margin-left:20px;font-size:12px}.bread-crub ul li:before{border-right:2px solid gray;border-bottom:2px solid gray;width:7px;height:7px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);content:"";display:inline-block;margin-right:10px}

